    position: absolute;
    background: rgba(55, 55, 55, 0.6);
    overflow: hidden;
    top: 0;
    height: 100%;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
}
.loading:before {
    content: '';
    display: inline-block;
    width: 50px;
    height: 50px;
